import { Component, OnInit, Input, Output } from '@angular/core';
import { DefaultObjDTO } from 'src/app/models/default-obj.dto';
import { PageDTO } from 'src/app/models/page-dto';
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { EventEmitter } from '@angular/core'
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MessageService } from 'primeng/api';
import { ModalController } from '@ionic/angular';
import { Subject, Subscription } from 'rxjs';

@Component({
  selector: 'app-paginacao',
  templateUrl: './paginacao.component.html',
  styleUrls: ['./paginacao.component.scss'],
})
export class PaginacaoComponent implements OnInit {

  selectedCar: any;

  @Input('objs')
  objs: DefaultObjDTO[] = [];

  @Input('emptyMessage')
  emptyMessage: string;

  @Input('add')
  add: boolean = false;

  @Input('paginaAtual')
  paginaAtual: number = 0;
  totalRecords: number = 0;
  pagina
  paginaTotal: number;

  @Input('id')
  idJson: any = '';
  @Input('nome')
  nomeJson: any = '';
  @Input('descricao')
  descricaoJson: any = '';
  @Input('tipo')
  tipoJson: any = '';
  @Input('imgURL')
  imgURLJson: any = '';

  @Input('closeBTN')
  closeBTN: boolean = false;

  @Input('isModal')
  isModal: boolean = false;

  defaultImg = "assets/imgs/dice.png";

  loading: boolean = false;

  cars: [];

  selectedObj;

  displayDialog: boolean;

  sortOptions: [];

  sortKey: string;

  sortField: string;

  sortOrder: number;

  objToModal;

  @Output()
  notifyParent: EventEmitter<any> = new EventEmitter();
  sendNotification() {
    this.notifyParent.emit(this.paginaAtual.toString());
  }

  @Output()
  getSelectedObj: EventEmitter<any> = new EventEmitter();
  selectObj(event: Event, obj) {
    /* this.selectedObj = obj;
     this.displayDialog = true;
     event.preventDefault();*/
    this.objToModal = obj;
    this.getSelectedObj.emit(obj);

    const flag = this.service.getInternalLoad();
    if (flag === true) {
      this.close();
    }

  }


  subscription: Subscription;

  constructor(private service: PaginacaoService,
    private toast: MessageService,
    private modalController: ModalController) {

    if (this.service.getConfiguration().use) {
      const aux = this.service.getConfiguration();
      this.objs = aux.objs;
      this.emptyMessage = aux.emptyMessage;
      this.add = aux.add;
      this.paginaAtual = aux.paginaAtual;
      this.idJson = aux.idJson;
      this.nomeJson = aux.nomeJson;
      this.descricaoJson = aux.descricaoJson;
      this.tipoJson = aux.tipoJson;
      this.imgURLJson = aux.imgURLJson;
      this.closeBTN = aux.closeBTN;
      this.isModal = aux.isModal;
    }

  }

  ngOnInit() {
    this.subscription = this.service.getRefresh().subscribe(message => {
      if (message === true) {
        //console.log(message)
        this.consultar();
      }
    });
    this.consultar();
  }



  consultar() {
    this.loading = true;

    const flag = this.service.getInternalLoad();

    /* if (flag == true) {
       const f = this.service.getFunctionPromissed();
       f(this.paginaAtual).subscribe(res => {
         this.pagina = res;
 
         this.paginaTotal = this.pagina.totalPages;
         this.totalRecords = this.pagina.totalElements;
         this.loadContent();
         this.loading = false;
       },
         error => {
           this.loading = false;
         });
     } else {*/
    this.service.getPromisse().subscribe(res => {
      this.pagina = res;

      this.paginaTotal = this.pagina.totalPages;
      this.totalRecords = this.pagina.totalElements;
      this.loadContent();
      this.loading = false;
    },
      error => {
        this.loading = false;
      });
    // }


  }

  loadContent() {
    this.objs = [];

    for (let i = 0; i < this.pagina.content.length; i++) {
      this.objs.push({
        id: this.getProperty(this.pagina.content[i], this.idJson),
        nome: this.getProperty(this.pagina.content[i], this.nomeJson),
        descricao: this.getProperty(this.pagina.content[i], this.descricaoJson),
        tipoRPG: this.getProperty(this.pagina.content[i], this.tipoJson),
        imgURL: this.getProperty(this.pagina.content[i], this.imgURLJson)
      });
    }
  }

  getProperty(json, field) {

    if (json == null || field == null) {
      return null;
    }

    var value = json;
    var fields = field.split(".");
    for (var i = 0; i < fields.length; i++) {

      value = value[fields[i]];

      if (value == null) {
        return null;
      }
    }

    return value;
  }



  onSortChange(event) {
    let value = event.value;

    if (value.indexOf('!') === 0) {
      this.sortOrder = -1;
      this.sortField = value.substring(1, value.length);
    }
    else {
      this.sortOrder = 1;
      this.sortField = value;
    }
  }

  onDialogHide() {
    this.selectedObj = null;
  }

  loadData(event) {
    const pageNumber = event.first / 10 || 0;
    this.paginaAtual = pageNumber;

    const flag = this.service.getInternalLoad();

    if (flag == true) {
      this.service.sendNewPageRequest(this.paginaAtual);
    }

    this.sendNotification();
    this.consultar();
  }

  clear(dv) {
  }

  copy(event, obj) {
    let flag = false;
    event.target.classList.forEach((classe: string) => {
      if (classe.includes('button')) {
        flag = true;
      }
    });

    if (!flag) {
      this.copyToClipboard(obj.id);
      this.callToast();
    }

  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  callToast() {
    this.toast.add({
      severity: 'success',
      summary: 'Copiado',
      detail: 'Código copiado para área de transferência'
    });
  }

  getTipo(obj) {
    if (isNaN(obj.tipoRPG)) {
      return obj.tipoRPG.substring(0, 20).concat('...')
    } else {
      return obj.tipoRPG
    }
  }

  close() {
    // console.log(this.objToModal);
    this.modalController.dismiss(this.objToModal);
  }
}
